<!-- 任务执行监督及评估页面 -->
<template>
  <div class="taskExecutionSupervisionEvaluation">
    <div class="content">
      <bdaTable
        class="content-table"
        :tableData="taskData"
        :headerColor="taskHeaderColor"
        @operate="taskOperate"
        :isPage="true"
        :pageConfig="pageConfig"
        @changePage="changePage"
      ></bdaTable>
    </div>
    <resultPreviewDialog
      v-if="resultDialogVisible"
      :visible.sync="resultDialogVisible"
      :showData="resultDialogData"
      :type="resultDialogType"
    ></resultPreviewDialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';
import bdaTable from "../../components/bda-table/bda-table";
import resultPreviewDialog from "./components/result-preview-dialog";

export default {
  components: {
    bdaTable,
    resultPreviewDialog,
  },
  name: "taskExecutionSupervisionEvaluation",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //推荐生成任务
      taskData: {},
      taskRes: false,
      taskHeaderColor: "",
      queryForm: {
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //执行结果查看
      resultDialogVisible: false,
      resultDialogType: "",
      resultDialogData: [],
      exeTypeOptions: [
        { name: "手工执行", value: '0' },
        { name: "自动执行", value: '1' },
      ]
    };
  },
  mounted() {
    this.getTaskData();

  },
  methods: {
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);

      if (type === "executeSupervision") {
        this.resultDialogType = "executeSupervision";
        this.resultDialogData = data
        this.resultDialogVisible = true;
      } else {
        this.resultDialogVisible = false;

      }
    },
    //关闭任务模态框
    taskDialogClose() {
      this.taskDialogVisible = false;
    },
    //提交审批
    submitApproval() {
      this.taskDialogVisible = false;
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        // { prop: "relationNumber", label: "关联关系编号" },
        { prop: "relationName", label: "关联关系名称" },
        { prop: "descibe", label: "关联关系描述" },
        // { prop: "exeType", label: "执行方式" },
        { prop: "exeTypeName", label: "执行方式" },
        { prop: "exeChannel", label: "执行渠道" },
        { prop: "exeTime", label: "执行完成时间" },
        // { prop: "executionStatus", label: "执行状态", width: "100px" },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          width: "310px",
          type: [
            {
              name: "执行结果监督", action: "executeSupervision",
            },
            // {
            //   name: "执行效果查看", action: "executeEffect"
            // },
          ]
        }
      ]
      this.taskMonitorList({ ...this.queryForm, menuType: this.menuType }).then(res => {
        this.pageConfig.total = res.total
        this.pageConfig.size = this.queryForm.size
        this.pageConfig.current = this.queryForm.current
        res.records.forEach(e => e.exeTypeName = this.translate(e.exeType, this.exeTypeOptions))
        this.taskData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        };
      }).catch((err) => {
        if (err) {
          this.taskData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })
    },
    //转义表格
    translate(item, data) {
      let name = ''
      data.forEach(e => {
        if (e.value === item) {
          name = e.name
        }
      })
      return name
    }
  }
};
</script>

<style scoped lang="scss">
.taskExecutionSupervisionEvaluation {
  width: 100%;
  height: 100%;
  .content {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: 100%;
    height: calc(100%);
    background: #ffffff;
    padding: 10px;
    box-sizing: border-box;
    ::v-deep .el-pagination__total {
      margin-left: 10px;
    }
    .table-box {
      height: calc(100% - 40px);
      .content-table {
        height: 100%;
      }
    }
    .content-pagination {
      margin-top: 10px;
      text-align: center;
    }
  }
}
</style>
