<!-- 审批执行任务 -->
<template>
  <div class="approvalTask">
    <div class="content">
      <bdaTable
        class="content-table"
        :tableData="taskData"
        :headerColor="taskHeaderColor"
        @operate="taskOperate"
        :isPage="true"
        :pageConfig="pageConfig"
        @changePage="changePage"
      ></bdaTable>
    </div>
    <operateDialog
      v-if="resultDialogVisible"
      :visible.sync="resultDialogVisible"
      :showData="resultDialogData"
      :type="resultDialogType"
    ></operateDialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';
import bdaTable from "../../components/bda-table/bda-table";
import operateDialog from "./components/operate-dialog";

export default {
  components: {
    bdaTable,
    operateDialog
  },
  name: "approvalTask",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //推荐生成任务
      taskData: {},
      taskRes: false,
      taskHeaderColor: "",
      queryForm: {
        relationNumber: '',
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //执行结果查看
      resultDialogVisible: false,
      resultDialogType: "",
      resultDialogData: {},
    };
  },
  mounted() {
    this.getTaskData();
  },
  methods: {
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);
      this.queryForm.relationNumber = data.relationNumber
      if (type === "approval") {
        this.getTaskDetailData(data)
      } else {
        if (type === 'approvalPass') {
          this.approvalResult('1')
        }
        if (type === 'approvalFail') {
          this.approvalResult('2')
        }

        this.resultDialogVisible = false;
      }

    },
    //审批通过/不通过
    approvalResult(val) {
      this.approvalThrough({
        relationNumber: this.queryForm.relationNumber,
        approvalState: val,
      }).then(res => {
        // if(val==='1')  
        // if(val==='2') 
        this.getTaskData()
      })
    },
    //关闭任务模态框
    taskDialogClose() {
      this.taskDialogVisible = false;
    },
    //提交审批
    submitApproval() {
      this.taskDialogVisible = false;
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        // { prop: "relationNumber", label: "关联关系编号" },
        { prop: "relationName", label: "关联关系名称" },
        { prop: "descibe", label: "关联关系描述" },

        {
          prop: "Operate",
          label: "任务结果",
          align: "center",
          width: "120px",
          type: [{ name: "任务详细信息", action: "approval" }]
        },
        { prop: "exeTime", label: "执行完成时间" },
        // { prop: "executionStatus", label: "执行状态", width: "100px" },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          width: "200px",
          type: [
            {
              name: "审批通过",
              action: "approvalPass",
            },
            {
              name: "审批不通过",
              action: "approvalFail",
            }
          ]
        }
      ]
      this.approvalExecutionList({ ...this.queryForm, menuType: this.menuType }).then(res => {
        this.pageConfig.total = res.total
        this.pageConfig.size = this.queryForm.size
        this.pageConfig.current = this.queryForm.current
        this.taskData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        };
        this.taskRes = true;
      }).catch((err) => {
        if (err) {
          this.taskData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })
    },
    //获取审批任务详细信息
    getTaskDetailData(data) {
      console.log(data, 'data')
      this.selectTaskDetail({
        relationNumber: data.relationNumber,
        targetId: data.targetId,
        recId: data.recId,
        tagId: data.tagId,
      }).then(res => {
        this.resultDialogType = "approval";
        this.resultDialogData = res
        this.resultDialogVisible = true;
      })
    },
  }
};
</script>

<style scoped lang="scss">
.approvalTask {
  width: 100%;
  height: 100%;
  .content {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: 100%;
    height: calc(100%);
    background: #ffffff;
    padding: 10px;
    box-sizing: border-box;
    .content-table {
      height: 100%;
    }
  }
  .task-dialog {
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-content {
      .input {
        width: 80%;
      }
    }
  }
}
</style>
