<template>
  <div class="recommendManagement">
    <div class="left-content">
      <div
        class="content-menu"
        @click="changeActive(index+1)"
        v-for="(item,index) in menuData"
        :class="active==='0'+(index+1)?'active':''"
        :key="index"
      >{{item.name}}</div>
    </div>
    <div class="right-content">
      <taskManagement :menuType="menuType" v-if="active==='01'"></taskManagement>
      <taskExecutionMonitoring :menuType="menuType" v-if="active==='02'"></taskExecutionMonitoring>
      <auditManagement :menuType="menuType" v-if="active==='03'"></auditManagement>
      <approvalTask :menuType="menuType" v-if="active==='04'"></approvalTask>
      <executionInformation :menuType="menuType" v-if="active==='05'"></executionInformation>
      <taskExecutionSupervisionEvaluation :menuType="menuType" v-if="active==='06'"></taskExecutionSupervisionEvaluation>
    </div>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
import taskManagement from './taskManagement';
import taskExecutionSupervisionEvaluation from './taskExecutionSupervisionEvaluation';
import approvalTask from './approvalTask';
import auditManagement from './auditManagement';
import executionInformation from './executionInformation';
import taskExecutionMonitoring from './taskExecutionMonitoring';
export default {
  components: {
    approvalTask,
    auditManagement,
    executionInformation,
    taskExecutionMonitoring,
    taskExecutionSupervisionEvaluation,
    taskManagement,
  },
  name: "recommendManagement",
  //   mixins: [Interface],

  data() {
    return {
      active: "01",
      menuType: "",
      menuData: [
        { name: '推荐生成任务管理', value: '01', },
        { name: '推荐生成任务执行监控', value: '02', },
        { name: '推荐生成结果审核校验', value: '03', },
        { name: '审批执行任务', value: '04', },
        { name: '任务执行信息录入与展示', value: '05', },
        { name: '任务执行监督及评估页面', value: '06', },
      ]
    }
  },
  created() {
    this.menuType = window.location.href.split('/')[window.location.href.split('/').length - 1]
  },
  methods: {
    changeActive(val) {
      this.active = '0' + val
    },
  },
}
</script>

<style scoped lang="scss">
.recommendManagement {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: transparent;
  display: flex;
  .left-content {
    width: 200px;
    height: 100%;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    // border: 1px solid #d9d9d9;
    margin: 10px;
    margin-right: 20px;
    .active {
      color: #409eff;
      background-color: #ecf5ff;
    }
    .content-menu {
      min-height: 5%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      cursor: pointer;
      //   background-color: #5e95cb;
      //   color: #fff;
      &:hover {
        background-color: #ecf5ff;
      }
    }
  }
  .right-content {
    width: calc(100% - 220px);
    height: 100%;
  }
}
</style>