<!-- 推荐生成任务执行监控 -->
<template>
  <div class="taskExecutionMonitoring">
    <div class="right">
      <div class="right-content">
        <bdaTable
          class="content-table"
          :tableData="taskData"
          :headerColor="taskHeaderColor"
          :indexWidth="0"
          :isPage="true"
          :pageConfig="pageConfig"
          @operate="taskOperate"
          @changePage="changePage"
        ></bdaTable>
      </div>
      <resultPreviewDialog
        v-if="resultDialogVisible"
        :showData="resultDialogData"
        :visible.sync="resultDialogVisible"
        :type="resultDialogType"
      ></resultPreviewDialog>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';

import bdaTable from "../../components/bda-table/bda-table";
import resultPreviewDialog from "./components/result-preview-dialog";

export default {
  components: {
    bdaTable,
    resultPreviewDialog
  },
  name: "taskExecutionMonitoring",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //过滤文本
      filterText: "",
      //树数据
      treeData: [],
      defaultProps: {
        children: "children",
        label: "label"
      },
      //推荐生成任务
      taskData: {},
      taskHeaderColor: "",
      queryForm: {
        current: 1,
        size: 10,
      },
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //执行结果查看
      resultDialogVisible: false,
      resultDialogType: "",
      resultDialogData: []
    };
  },
  mounted() {
    this.getTaskData();
  },
  methods: {
    //点击树节点
    nodeClick(obj, node) {
      console.log(obj, node, "obj, node");
    },
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);
      if (type === "executeResult") {
        this.resultDialogType = 'executeResult'
        this.resultDialogData = data;
        this.resultDialogVisible = true;
      } else {
        this.runExecute(data)
        this.resultDialogVisible = false;
      }
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //树过滤时触发
    filterNode(value, data) {
      console.log(value, data, "value, data");
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },

    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        { prop: "relationNumber", label: "关系编号" },
        { prop: "relationName", label: "关联关系名称" },
        { prop: "descibe", label: "关联关系描述" },
        { prop: "updateCycle", label: "更新周期" },
        { prop: "createTime", label: "创建时间" },
        { prop: "updateTime", label: "更新时间" },
        { prop: "executeStartTime", label: "执行开始时间" },
        { prop: "executeEndTime", label: "执行完成时间" },
        { prop: "state", label: "执行状态", width: "100px" },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          width: "200px",
          type: [
            { name: "立即执行", action: "execute" },
            { name: "执行结果查看", action: "executeResult" }
          ]
        }]
      this.getTaskExecuteMonitor({ menuType: this.menuType, ...this.queryForm }).then(res => {
        this.pageConfig.total = res.total
        this.pageConfig.size = this.queryForm.size
        this.pageConfig.current = this.queryForm.current
        this.taskData = {
          tableData: [...res.list],
          columnNameData: columnNameData
        };
      })

    },
    //立即执行
    runExecute(data) {
      this.runTask({ relationNumber: data.relationNumber }).then(res => {
        this.$message.success('正在执行中')
      })
    }
  }
};
</script>

<style scoped lang="scss">
.taskExecutionMonitoring {
  width: 100%;
  height: 100%;
  display: flex;

  .left {
    width: 20%;
    margin-right: 20px;
    background: #ffffff;
    border: 1px solid #e6e6e6;
    padding: 10px;
    box-sizing: border-box;
    ::v-deep .el-tree-node__content:hover {
      background-color: #f2f8ff;
    }
    .left-header {
      display: flex;
      align-items: center;
      .input {
        width: 90%;
      }
      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 10%;
        font-size: 16px;
        color: #1a84f4;
        cursor: pointer;
      }
    }
  }
  .right {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: calc(100%);
    // padding: 10px;
    // box-sizing: border-box;

    .right-content {
      width: 100%;
      height: calc(100%);
      background: #ffffff;
      border: 1px solid #e6e6e6;
      padding: 10px;
      box-sizing: border-box;
      ::v-deep .el-pagination__total {
        margin-left: 10px;
      }
      .content-table {
        height: 100%;
      }
      .content-pagination {
        margin-top: 10px;
        text-align: center;
      }
    }
  }
}
</style>
